<template>
    <div class="wallet">
       <div class="header-block">
            <p class="wallet-header">{{$localizeFilter(isDemo ? 'wallet_demo' : 'wallet')}}</p>
            <p class="wallet-text">{{$localizeFilter('wallet_text')}}</p>
        </div>

        <Balance class="balance" :coinTicker="coinTicker" :data="walletData"/>

        <CoinsTable style="margin-top:10px;" :coins="coins"/>

        <LinkTelegramWindow v-if="needToLinkTelegram" @telegramLinked="telegramLinked"/>
        <Modal v-if="showModal && !needToLinkTelegram" :type="'welcomeBonus'" :data="welcomeBonusData" @closeModal="closeModal"/>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import Modal from "@/components/modals/Modal.vue"
import LinkTelegramWindow from '@/components/LinkTelegramWindow.vue'
import Balance from '@/components/Balance'
import CoinsTable from '@/components/CoinsTable'
import localizeFilter from '@/filters/localize.filter'

export default defineComponent({
    props: {
        isDemoOverride: {
            type: Boolean,
            default: false
        },
        buyCoinTicker: {
            type: String,
            default: null
        }
    },
    components: {
        Modal, LinkTelegramWindow, Balance, CoinsTable
    },
    setup() {

    },
    data: () => ({
        showModal: false,
        welcomeBonusData: null
    }),
    computed: {
        needToLinkTelegram(){
            return this.$store.state.info?.profile?.needToLinkTelegram || false
        },
        isDemo() {
            return this.$store.getters.isDemo
        },
        coinTicker() {
            return this.$store.getters.walletData?.coinTicker || ''
        },
        walletData() {
            return this.$store.getters.walletData || {}
        },
        coins() {
            return this.$store.getters.walletCoins || []
        },
    },
    methods: {
        setupModal() {
            this.welcomeBonusData = JSON.parse(localStorage.exchangeWelcomeBonusData)
            if (this.welcomeBonusData != null && this.welcomeBonusData != undefined) {
                this.showModal = true
            }
        },
        async telegramLinked() {
            let profile = await this.$store.dispatch('getProfile')
            if (!profile.needToLinkTelegram) {
                this.$message(this.$localizeFilter('link_telegram_success'))
                this.setupModal()
            }
        },
        closeModal() {
            this.showModal = false
            if (this.$store.getters.profile?.buyCoinTicker) {
                this.buyCoin(this.$store.getters.profile.buyCoinTicker)
            }
        },
        async buyCoin(coinTicker) {
            await this.$store.dispatch('setPaymentCoinTicker', {coinTicker: coinTicker})
            this.$nextTick(()=>{
                this.$store.dispatch('setShowPaymentCoinModule', {value: true})
            })
        },
        async loadData() {
            try {
                await this.$store.dispatch('getWalletData')
                // remove this:
                // await this.$store.dispatch('getWalletCoins')
            }
            catch (e) {
                console.log(e)
                this.$error(e)
            }
        }
    },
    mounted() {
        this.$nextTick(()=> {
            this.$store.commit('SET_DEMO', {isDemo: this.isDemoOverride})
            if (this.buyCoinTicker && (this.$store.getters.profile?.needToLinkTelegram !== true)) {
                this.buyCoin(this.buyCoinTicker)
            }
        })
        this.loadData()
    }
})
</script>

